.flex {
    display: flex;
    margin: 150px 0% 0% 450px;
    padding: 0%;
    flex-direction: column;
    align-items: center;
}

.title {
    font-family: Helvetica, sans-serif;
    font-size: 45px;
    font-weight: 700;
    text-align: center;
    line-height: 40px;
    color: var(--white);
    padding: 0%;
    width: 400px;
    margin-top: 100px;
    margin-bottom: 0px;

}

.subtitle {
    font-size: 35px;
    font-weight: 700;
    text-align: center;
    line-height: 40px;
    color: var(--white);
    padding: 0%;
    margin-bottom: 5px;
    margin-left: auto;
    margin-right: auto;
}

.btn {
    cursor: pointer;
    display: inline-block;
    width: 120px;
    height: 30px;
    font-size: 15px;
    font-weight: 700;
    cursor: pointer;
    padding: 3px 7px;
    margin: 0%;
    margin-right: 15px;
    border: 0px;
    border: 1px solid var(--black);
}

.section {
    margin-top: 20px;
    margin-left: auto;
    margin-right: auto;
}